.categories {
    width: 87.5%;
    margin: 5vh auto;
    .heading {
        padding-bottom: 0.25rem;
        font-size: 2.1rem;
        border-bottom: 1px solid rgb(170, 170, 170);
    }
    .categories-wrapper {
        margin: 1rem auto;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        .category-container {
            color: rgb(50, 50, 50);
            width: 15%;
            margin: 0 0.25rem;
            display: flex;
            flex-direction: column;
            align-items: baseline;
            border-radius: 8px;
            border: 1px solid rgb(170, 170, 170);
            padding: 0.6rem 1rem;
            &:first-of-type {
                margin-left: 0;
            }
            .category-name {
                font-size: 1.075rem;
                line-height: 1.4rem;
                font-weight: 600;
                display: flex;
                align-items: center;
                justify-content: center;
                .icon {
                    font-size: 1.1rem;
                    color: rgb(100, 100, 100);
                    margin-right: 0.5rem;
                }
            }
            .cost {
                margin: 1rem 0 0;
                font-weight: 600;
            }
        }
    }
}